.dayContainer {
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: space-between;
    padding: 20px;
    width: 95%;
    font-family: "Courier New", Courier, monospace;
}

.roundedBox {
    display: block;
    border-radius: 20px;
    padding: 10px;
    width: 95%;
    margin: 5px;
    font-family: "Courier New", Courier, monospace;
}

.dayBox {
    background: aquamarine;
    color: black;
}

.eventBox {
    background: lightskyblue;
    margin-left: 10px;
    color: black;
    font-family: "Courier New", Courier, monospace;
}

.dayBound {
    border: 2px solid black;
    margin-bottom: 100px;
}
.resContainer {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin: 10px;
    padding: 10px;
    width: 93%;
    border: 2px solid rgb(54, 95, 122);
    border-radius: 20px;
}

.resElem {
    display: block;
    padding: 10px;
    width: 95%;
    margin: 5px;
    border-radius: 20px;
    background: lightcyan;
    border: 2px solid rgb(54, 95, 122);
    font-family: "Courier New", Courier, monospace;
}

.restHeader {
    color: black;
}

.questionBox {
    display: block;
    border-radius: 20px;
    padding: 10px;
    width: 95%;
    margin: 5px;
    margin-bottom: 15px;
    background: lightskyblue;
    color: black;
    font-family: "Courier New", Courier, monospace;
}

.formContainer {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin: 10px;
    padding: 10px;
    width: 93%;
    border: 2px solid rgb(54, 95, 122);
    border-radius: 20px;
}

.formElem {
    display: block;
    padding: 10px;
    width: 95%;
    margin: 5px;
}

.formHeader {
    color: black;
    font-family: "Courier New", Courier, monospace;
}


.grid-container {
    display: block;
    border-radius: 20px;
    padding: 10px;
    width: 95%;
    margin: 5px;
    margin-bottom: 15px;
    background: rgb(5, 175, 175);
    color: black;
    font-family: "Courier New", Courier, monospace;
}

.announce-container {
    display: flex;
    flex-direction: column;
    align-content: center;
    background-color: aliceblue;
    margin-bottom: 30px;
    padding: 10px;
    width: 93%;
    border: 2px solid rgb(54, 95, 122);
    border-radius: 20px;
}

.horiz-container {
    display: flex;
    flex-direction: row;
    align-content: center;
    margin: 10px;
    padding: 10px;
}

.announce-header {
    display: block;
    flex-grow: 1;
    color: red;
}

